/* @import './selectbox.scss'; */
body {
  min-width: 16.666666666666664vw;
  font-family: "Klein";
  font-style: normal;
  font-weight: normal;
  @include clear;
  scroll-behavior: smooth;
  color: $main;
  overflow-x: hidden;
  width: 100%;
}
* {
  font-family: "Klein";
  font-style: normal;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: none !important;
  /*   line-height: 1.2; */
}
/* main {
		overflow: hidden;
}
*/
a {
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  color: #04a3e9;
  text-decoration: none;
}
a:hover,
a:focus {
  text-decoration: none;
}
img {
  max-width: 100%;
  max-height: 100%;
  width: initial;
  height: initial;
}

@media (min-width: 600px) {
  .onlyMobile {
    display: none !important;
  }
}
@include small-screen {
  .onlyMobile {
    display: initial;
  }
  .onlyDesktop {
    display: none !important;
  }
}

@media (min-width: 600px) {
  .onlyMobile {
    display: none !important;
  }
}

@media (min-width: 1000px) {
  .onlyTablet {
    display: none !important;
  }
}
@include medium-screen {
  .onlyTablet {
    display: initial;
  }
  .hideTablet {
    display: none !important;
  }
}
.container {
  @include container;
}

.logo {
  width: 17vw;
}

