$dropdownblock_top: get-vw-large(33px);
.dropdownblock {
  position: absolute;
  top: calc(100% + #{$dropdownblock_top});
  min-width: get-vw-large(252px);
  display: none;
}
.dropdownblock.active {
  display: block;
  z-index: 999;
}
.dropdownblock__wrapper {
  background: #ffffff;
  -webkit-box-shadow: 5px 5px 60px rgba(36, 40, 43, 0.1);
  box-shadow: 5px 5px 60px rgba(36, 40, 43, 0.1);
  border-radius: get-vw-large(6px);
  padding: get-vw-large(29px) get-vw-large(30px);
}
.dropdownblock__links {
  ul {
    @include clear;
    li {
      margin-bottom: get-vw-large(29px);
      width: 100%;
      &:last-child {
        margin-bottom: 0;
      }
      a {
        font-style: normal;
        font-weight: 700;
        font-size: get-vw-large(16px);
        line-height: get-vw-large(21px);
        /* identical to box height */
        color: #212531;
      }
    }
  }
}

.signout a {
  color: $main4 !important;
}

$messageblock_top: get-vw-large(20px);
.messageblock {
  min-width: get-vw-large(512px);
  right: 0;
  top: calc(100% + #{$messageblock_top});
}
.messageblock__items {
  max-height: get-vw-large(326px);
  overflow-x: hidden;
  overflow-y: auto;
  padding-right: get-vw-large(10px);
}
.messageblock__item {
  margin-bottom: get-vw-large(34px);
  &:last-child {
    margin-bottom: 0;
  }
}
.messageblock__status {
  min-width: get-vw-large(6px);
  min-height: get-vw-large(6px);
  margin-right: get-vw-large(8px);
  background: rgba(33, 37, 49, 0.2);
  border-radius: 50%;
}
.messageblock__item_unread {
  .messageblock__status {
    background: #30ca4c;
  }
}
.messageblock__img {
  width: get-vw-large(46px);
  height: get-vw-large(46px);
  margin-right: get-vw-large(21px);
  border-radius: 50%;
  overflow: hidden;
  position: relative;

  img {
    position: absolute;
    left: -9999px;
    right: -9999px;
    bottom: -9999px;
    top: -9999px;
    margin: auto;
    height: 100%;
    max-width: initial;
  }
}
.messageblock__text {
  font-style: normal;
  font-weight: 700;
  font-size: get-vw-large(16px);
  line-height: 100%;
  /* or 16px */
  margin-bottom: get-vw-large(10px);

  color: #212531;
}
.messageblock__info {
  font-style: normal;
  font-weight: 400;
  font-size: get-vw-large(14px);
  line-height: 100%;
  /* identical to box height, or 14px */

  color: rgba(33, 37, 49, 0.5);
}

/* width */
::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
/* Track */
::-webkit-scrollbar-track {
  background: #eaecf0;
  border-radius: 10px;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: #30ca4c;
  border-radius: 10px;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #ccc;
}

$mobmenuheight: get-vw-small(116px);

.mobilemenu {
