  align-items: center;
  padding: 1vw;
  margin-bottom: 1vh;
}

.payment-modal__textblock_green {
  background: linear-gradient(
    to right,
    rgba(48, 202, 76, 0.3),
    rgba(48, 202, 76, 0)
  );
}

.payment-modal__textblock_yellow {
